<template>
  <div class="wrapDiv">
    <div class="top">
      <el-image src="/static/images/bg_active_list.png" alt="" />
      <ul>
        <li>Event events</li>
        <li>俱乐部活动赛事</li>
      </ul>
    </div>

    <div class="content">
      <ul class="titles">
        <li
          v-for="(tab, index) in tabs"
          :key="index"
          @click="toggle(index)"
          :class="{ active: active == index }"
        >
          {{ tab.view }}
        </li>
      </ul>

      <div class="Competition">
        <ul class="imgDiv">
          <li
            v-for="(item, index) in datas.data"
            :key="index"
            @click="toDetails(item.id)"
          >
            <div class="comp-img">
              <img :src="item.img_list_path" alt="" />
            </div>
            <div class="comp-text">
              <div :title="item.title" class="omit">{{ item.title }}</div>
              <div :title="item.sub_title" class="omit">
                {{ item.sub_title }}
              </div>
            </div>
          </li>
        </ul>
      </div>

      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="prev, pager, next"
        :current-page="currentPage"
        :page-size="8"
        :pager-count="5"
        :total="Number(datas.page_count)"
        v-if="datas.page_count > 8"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { GET_ACTIVE } from "@/api/mall";
export default {
  name: "activityLists",
  data() {
    return {
      active: 0,
      currentView: "全部",
      tabs: [
        {
          view: "全部"
        },
        {
          view: "活动"
        },
        {
          view: "赛事"
        }
      ],
      datas: {},
      currentPage: 1, //初始页
      pagesize: 8 // 每页的数据
    };
  },
  beforeRouteEnter(to, from, next) {
    next(v => {
      console.log("next", v, from);
      if (from.query.page) {
        v.currentPage = parseInt(from.query.page);
        v.active = from.query.active;
      }
    });
  },
  methods: {
    listData(isType, pages) {
      GET_ACTIVE({
        is_match: isType,
        page: pages
      })
        .then(res => {
          this.datas = res;
          console.log(111, this.datas);
        })
        .catch(err => {});
    },
    toggle(i) {
      let that = this;
      that.active = i;
      that.currentPage = 1;
      that.listData(i, this.currentPage);
    },
    toDetails(e) {
      this.$router.push({
        path: "/activityDetails",
        query: {
          id: e,
          page: Number(this.currentPage),
          active: this.active
        }
      });
    },
    handleSizeChange(size) {
      this.pagesize = size;
      console.log(this.pagesize);
    },
    handleCurrentChange(currentPage) {
      console.log("currentPage", currentPage);
      let that = this;
      that.currentPage = currentPage;
      that.listData(that.active, this.currentPage);
    }
  },
  mounted() {
    this.listData(this.active, this.currentPage);
  }
};
</script>

<style lang="scss" type="text/scss" scoped>
.wrapDiv {
  width: 100%;
  height: 100%;
  .el-pagination {
    width: 80%;
    margin-left: 10%;
    text-align: right;
  }
  .active {
    color: #b71d2d;
    border-bottom: 3px solid #b71d2d;
    padding-bottom: 22px;
  }
  .top {
    width: 100%;
    // height: 694px;
    position: relative;
    img {
      width: 100%;
      // height: 100%;
    }
    ul {
      position: absolute;
      left: 266px;
      top: 320px;
      color: #fff;
      font-weight: bold;
      li:nth-of-type(1) {
        font-size: 42px;
      }
      li:nth-of-type(2) {
        font-size: 28px;
        margin-top: 30px;
      }
    }
  }
  .content {
    width: 100%;
    margin-bottom: 90px;
    .titles {
      font-size: 16px;
      color: #000000;
      display: flex;
      justify-content: center;
      margin-top: 28px;
      li {
        cursor: pointer;
        &:nth-of-type(2) {
          margin: 0 5%;
        }
      }
    }
    .Competition {
      background: #fff;
      .imgDiv {
        width: 80%;
        margin: 68px auto 0;
        display: flex;
        flex-wrap: wrap;
        li {
          width: 24%;
          color: #000000;
          margin-right: 1%;
          margin-bottom: 20px;
          transition: all 0.6s;
          cursor: pointer;
          .comp-img {
            overflow: hidden;
            img {
              width: 100%;
              transition: all 0.6s;
            }
          }
          .comp-text {
            display: flex;
            flex-direction: column;
            text-align: left;
            background: #fff;
            div:nth-of-type(1) {
              font-weight: bold;
              font-size: 18px;
              padding: 18px 0 0 0;
            }
            div:nth-of-type(2) {
              font-size: 12px;
              padding-top: 18px;
              margin-bottom: 20px;
            }
          }
          &:hover {
            box-shadow: 0px 10px 13px 0px rgba(0, 0, 0, 0.12);
            img {
              transform: scale(1.1);
            }
          }
          &:nth-child(4n) {
            margin-right: 0;
          }
        }
      }
    }
  }
}
/* 移动端  */
@media all and (max-width: 768px) {
  .wrapDiv {
    width: 100%;
    height: 100%;
    .el-pagination {
      width: 90%;
      margin-left: 2.5%;
      margin-bottom: 20px;
      text-align: center;
    }
    .active {
      color: #b71d2d;
      border-bottom: 3px solid #b71d2d;
      padding-bottom: 10px;
    }
    .top {
      width: 100%;
      position: relative;
      img {
        width: 100%;
      }
      ul {
        display: none;
      }
    }
    .content {
      width: 100%;
      margin-bottom: 0;
      .titles {
        font-size: 16px;
        color: #000000;
        display: flex;
        justify-content: center;
        margin-top: 28px;
        li {
          cursor: pointer;
          &:nth-of-type(2) {
            margin: 0 5%;
          }
        }
      }
      .Competition {
        background: #fff;
        .imgDiv {
          width: 95%;
          margin: 30px auto 0;
          display: flex;
          justify-content: space-between;
          flex-wrap: wrap;
          li {
            width: 48.5%;
            color: #000000;
            // margin-right: 2%;
            margin-bottom: 20px;
            transition: all 0.6s;
            cursor: pointer;
            .comp-img {
              overflow: hidden;
              img {
                width: 100%;
                transition: all 0.6s;
              }
            }
            .comp-text {
              display: flex;
              flex-direction: column;
              text-align: left;
              background: #fff;
              div:nth-of-type(1) {
                font-weight: bold;
                font-size: 18px;
                line-height: 25px;
                padding: 18px 0 0 0;
              }
              div:nth-of-type(2) {
                font-size: 12px;
                padding-top: 18px;
                margin-bottom: 20px;
                line-height: 15px;
              }
            }
          }
        }
      }
    }
  }
}
</style>



